* {
  box-sizing: border-box; }

html {
  font-family: 'SF Pro display';
  font-size: 15px; }

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0; }

body {
  min-height: 100vh;
  padding: 0;
  margin: 0; }

ul, ol {
  padding: 0;
  margin: 0; }

/* Page-nav */
.page-nav {
  background-color: rgba(45, 45, 45, 0.98);
  display: flex;
  justify-content: center;
  height: 50px;
  padding: 0 8px; }

.page-nav__menu {
  display: flex;
  justify-content: space-between;
  max-width: 960px;
  width: 100%;
  list-style: none; }

.page-nav__link {
  height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  text-decoration: none; }

.page-nav__switcher {
  background-color: transparent;
  border: none;
  height: 20px;
  width: 20px;
  outline: none;
  font-size: 1rem;
  position: relative; }

.page-nav__img {
  height: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  background-color: transparent;
  border: none;
  position: relative; }

.page-nav__switcher:before {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #FFF;
  position: absolute;
  top: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav__switcher:after {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #FFF;
  position: absolute;
  bottom: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav.opened .page-nav__switcher:before {
  transform: translateY(5px) rotate(45deg); }

.page-nav.opened .page-nav__switcher:after {
  transform: translateY(-4px) rotate(-45deg); }

.page-nav__controls {
  display: none;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.page-nav.opened .page-nav__img-link {
  display: none; }

/*Chapter-nav*/
.chapter-nav {
  background: #f6f6f6;
  display: flex;
  justify-content: center;
  height: 120px; }

.chapter-nav__content {
  display: flex;
  list-style: none;
  justify-content: space-between;
  max-width: 820px;
  width: 100%;
  margin: 15px; }

.chapter-nav__item {
  text-align: center; }

.chapter-nav__item_link {
  text-decoration: none;
  color: #111; }

.chapter-nav__item_label {
  font-size: 8px;
  color: rgba(0, 0, 0, 0.8); }

/* Section*/
.section {
  margin: 1rem auto;
  box-sizing: border-box;
  position: relative; }

.section-content {
  box-sizing: border-box;
  margin: 1rem auto;
  position: relative;
  overflow: hidden; }

.section-description {
  text-align: center; }

.section-description_content {
  margin: 0 auto; }

.row-reverse {
  flex-direction: row-reverse; }

.grid-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  height: 100%; }

.section-header_large {
  font-size: 3.5rem; }

.section-header {
  font-size: 2.5rem; }

.section-header_small {
  font-size: 1.6875rem; }

.section-header_description {
  font-size: 1.3125rem; }

.section_text {
  margin: 0 auto;
  text-align: center; }

.section-availability {
  color: #888;
  padding-top: 0.75rem; }

.section-availability > span {
  padding: 0 1rem; }

.section-link {
  display: inline-block;
  padding-top: 0.75rem;
  color: #0070c9;
  font-size: 1.3rem;
  position: relative;
  text-decoration: none; }

.section-link:after {
  content: '>';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(150%, -17%);
  font-size: 1rem; }

.section-img {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

