  display: flex;
  align-items: stretch;
  flex-direction: column;
  justify-content: center; }

.section-ios .section-img {
  position: absolute;
  right: -10%;
  top: 50%;
  transform: translateY(-50%);
  display: inline-flex;
  background-image: url(./assets/images/ios_large_2x.jpg);
  width: 828px;
  height: 527px;
  background-size: 828px 527px;
  margin-top: 0; }

@media (max-width: 767px) {
  .page-nav__controls {
    display: flex;
    position: relative; }
  .page-nav__first-img {
    position: absolute; }
  .page-nav__second-img {
    position: absolute;
    left: 90%; }
  .page-nav.opened {
    background-color: black; }
  .page-nav__menu {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100vw;
    min-height: 100vh;
    background-color: black;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 45px;
    display: flex;
    visibility: hidden;
    transition: all 200ms;
    transform: scaleY(0);
    transform-origin: top; }
  .page-nav.opened .page-nav__menu {
    visibility: visible;
    transform: scaleY(1); }
  .page-nav__link {
    padding: 15px 0;
    border-bottom: 1px solid #292929;
    opacity: 0;
    transition: opacity 150ms 30ms; }
  .page-nav.opened .page-nav__link {
    opacity: 1; } }
